import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import RackUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/rack/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/racks";


class UpdateRack extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editRack } = this.props;
    const { id } = this.props.match.params;
    editRack(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.rack.error) {
      notification("error", this.props.rack.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.rack.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateRack } = this.props;
    const { id } = this.props.match.params;
    updateRack(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="racks.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.rack.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <RackUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                rack={this.props.rack}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    rack: state.rack
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateRack);
