import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Row, Col , Alert } from "antd";

const { Option } = Select;
const { TextArea } = Input;

class RackUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { rack } = this.props;
    const { loading } = rack;
    const data = rack.rack ? rack.rack : [];
    const used = data.is_used;
    const error = rack.errorField ? rack.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="racks.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="racks.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.pageHeader" />}
            >
              {getFieldDecorator("id_location", {
                initialValue: data.id_location,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={this.props.rack.loading}
                  showSearch
                  optionFilterProp="children"
                  
                  disabled={used ? true : false}
                >
                  {rack.locations.map((data, key) => (
                    <Option key={key} value={data.id_location}>
                      { data.location_code + " (" + data.location_name +")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="racks.add.code" />}
              validateStatus={
                error.rack_code || getFieldError("rack_code")
                  ? "error"
                  : null
              }
              help={
                error.rack_code
                  ? error.rack_code
                  : getFieldError("rack_code")
              }
            >
              {getFieldDecorator("rack_code", {
                initialValue: data.rack_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.name" />}
            >
              {getFieldDecorator("rack_name", {
                initialValue: data.rack_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input
                disabled={used ? true : false}
              />)}
            </Form.Item>
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
              })(<TextArea 
                  autosize={{ minRows: 3 }}
              />)}
            </Form.Item>
          
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            // disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedRackUpdateForm = Form.create({ name: "update_rack" })(RackUpdateForm);

export default WrappedRackUpdateForm;
