import React from "react";
import { ComponentTitleWrapper } from "./pageHeader.style";
import { Divider } from "antd";

export default props =>
  props.title ? (
    <div>
      <div className="isoPageHeaderWrapper">
        <h1 className="isoCode">{props.title}</h1>
        {props.children}
      </div>
      <Divider
        style={{ margin: props.marginBottom ? "10px 0 20px 0" : "10px 0" }}
      />
    </div>
  ) : (
    <ComponentTitleWrapper className="isoComponentTitle">
      {props.children}
    </ComponentTitleWrapper>
  );
