const actions = {
  getRacks: (page, search, filter) => ({
    type: 'GET_RACKS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createRack: () => ({
    type: 'CREATE_RACK_REQUEST'
  }),
  storeRack: data => ({
    type: 'STORE_RACK_REQUEST',
    payload: { body: data }
  }),
  editRack: id_rack => ({
    type: 'EDIT_RACK_REQUEST',
    payload: { id_rack: id_rack }
  }),
  updateRack: (id_rack, data) => ({
    type: 'UPDATE_RACK_REQUEST',
    payload: {
      id_rack: id_rack,
      body: data
    }
  }),
  showRack: id_rack => ({
    type: 'SHOW_RACK_REQUEST',
    payload: { id_rack: id_rack }
  }),
  deactivateRack: id_rack => ({
    type: 'DEACTIVATE_RACK_REQUEST',
    payload: { id_rack }
  }),
  activateRack: id_rack => ({
    type: 'ACTIVATE_RACK_REQUEST',
    payload: { id_rack }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
